
.chartsmall {
  padding: 15px;
  margin: 10px;
  background: rgba(111, 111, 111, 0.19);
  display: block;
  float: left;
  width: 570px;
}
.chartsmall h3 {
  text-transform: uppercase;
  font-size: 1.2em;
  color: #696969;
  margin: 0;
  margin-bottom: 10px;
  padding-left: 5px;
  padding-top: 5px;
}
.chartsmall path {
  fill: none;
  stroke: #6cf391;
  stroke-width: 0.2em;
}
.chartsmall .axis line {
  /*stroke: #444444;*/
  stroke: none;
  stroke-width: 1px;
}
.chartsmall .axis text {
  fill: #999;
  /*font-family: 'Cousine';*/
  font-family: 'Titillium Web';
  text-transform: uppercase;
  font-size: 1em;
}
.chartsmall .axisx line {
  stroke-dasharray: 1 1;
  display: none;
}



