
.chart {
  padding: 3em;
  margin: 2em;
  background: rgba(113, 113, 113, 0.17);
  float: left;
}
.chart h3 {
  text-transform: uppercase;
  font-size: 1.9em;
  color: #696969;
  margin: 0;
  margin-bottom: 1em;
}
.chart path {
  fill: none;
  stroke: #6cf391;
  stroke-width: 0.2em;
}
.chart .axis line {
  stroke: #444444;
  stroke-width: 2px;
}
.chart .axis text {
  fill: #999;
  font-family: 'Cousine';
  font-size: 1.5em;
}
.chart .axisx line {
  stroke-dasharray: 5 5;
}



